html {
  overflow-y: hidden;
  overflow-x: hidden;
  scrollbar-width: thin;
  scrollbar-color: #4794cf #78c045;
}

html::-webkit-scrollbar {
  width: 8px;
}

html::-webkit-scrollbar-track {
  background-color: #78C045;
}

html::-webkit-scrollbar-thumb {
  background-color: #4794CF;
}

body {
  font-family: 'tj', sans-serif;
  overflow-x: hidden;
}

* {
  font-family: inherit;
}

input, a, button {
  font-family: 'tj', sans-serif;
}

input:focus, a:focus, button:focus {
  outline: none;
  text-decoration: none;
}

input:hover, a:hover, button:hover {
  text-decoration: none;
}

button {
  cursor: pointer;
}

button:focus, button:active {
  outline: none;
  box-shadow: none;
}

ul.res {
