.navbar1 > ul > li.drp {
  position: relative;
}

.navbar1 > ul > li.drp .dropDown {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: #EC1B8D;
  width: 166px;
  padding: 5px 11px;
  z-index: 9;
  transform: translateX(50px);
  transition: 0.4s cubic-bezier(0.55, 0.085, 0.68, 0.53);
  opacity: 0;
  visibility: hidden;
}

html[dir="ltr"] .navbar1 > ul > li.drp .dropDown {
  left: 0;
  right: unset;
  transform: translateX(-50px);
}

.navbar1 > ul > li.drp .dropDown ul li {
  position: relative;
  padding: 6px 10px;
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
  transition: .5s;
  overflow: hidden;
}

.navbar1 > ul > li.drp .dropDown ul li:not(:last-child) {
  border-bottom: 1px solid #EE3198;
}

.navbar1 > ul > li.drp .dropDown ul li:after, .navbar1 > ul > li.drp .dropDown ul li:before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #F045A2;
  position: absolute;
  transition: .6s;
}

.navbar1 > ul > li.drp .dropDown ul li:after {
  top: calc(100% - 1px);
  left: -100%;
}

.navbar1 > ul > li.drp .dropDown ul li:before {
  top: 0;
  right: -100%;
}

.navbar1 > ul > li.drp .dropDown ul li a {
  color: #FFF;
  font-size: 14px;
  font-family: "tjMd";
  padding: 0;
}

.navbar1 > ul > li.drp .dropDown ul li:hover {
  background-color: #EE3198;
}

.navbar1 > ul > li.drp .dropDown ul li:hover:after {
  left: 0;
}

.navbar1 > ul > li.drp .dropDown ul li:hover:before {
  right: 0;
}

.navbar1 > ul > li.drp:hover .dropDown {
  transform: translateX(0px);
  opacity: 1;
  visibility: visible;
}

html[dir="ltr"] .navbar1 > ul > li.drp:hover .dropDown {
  transform: translateX(0px);
}

