@import 'main';
// End Of Import

// Preloader
.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color-prim;
    @include Flex;
    z-index: 99999;
  
    svg{
        position: relative;
        width: 150px;
        height: 150px;
        animation: rotate 2s linear infinite;

        circle{
            width: 100%;
            height: 100%;
            fill: none;
            stroke-width: 10;
            stroke: #FFF;
            stroke-linecap: round;
            transform: translate(5px, 5px);
            stroke-dasharray: 440;
            stroke-dashoffset: 440;
            animation: animate 4s linear infinite;
        }
    }
}

@keyframes animate{
    0%, 100%{
      stroke-dashoffset: 440
    }
    50%{
      stroke-dashoffset: 0
    }
    
    50.1%{
      stroke-dashoffset: 880
    }
}
  
@keyframes rotate{
0%{
    transform: rotate(0)
}
100%{
    transform: rotate(360deg)
}
}

// Mob Nav
.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 9999;
    transition: .5s;
    opacity: 0;
    visibility: hidden;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: -100%;
    width: 255px;
    height: 100%;
    background-color: #FFF;
    z-index: 99999;
    overflow-y: auto;
    transition: .5s;

    html[dir="ltr"] & {
        left: -256px
    }

    &.open{
        right: 0;

        html[dir="ltr"] & {
            right: unset;
            left: 0;
        }
    }

    .user-sec{
        padding: 17px;
        @include Flex;
        justify-content: space-between;
        
        .sign{
            
            a{
                font-size: 14px;
                font-family: "tjMd";
                color: #1a3766;
            }
        }
        
        .myCart{
            @include Flex;
            width: 45px;
            height: 45px;
            border-radius: 50%;
            background-color: #BFE7F0;
            color: #1a3766;
            font-size: 17px;
        }
    }

    & > ul {
        display: flex;
        align-items: center;
        flex-direction: column;
        // height: 100%;
        width: 255px;
        // overflow: auto;
        padding: 0px;

        & > li{
            position: relative;
            padding: 7px 15px;
            width: 100%;

            &:not(:last-child){
                margin-bottom: 3px;
            }

            &:nth-child(1){
                background-color: #78C045;
                &:hover{
                    background-color: darken(#78C045, 7%)
                }
            }
            &:nth-child(2){
                background-color: #EC1B8D;
                &:hover{
                    background-color: darken(#EC1B8D, 7%)
                }
            }
            &:nth-child(3){
                background-color: #4794CF;
                &:hover{
                    background-color: darken(#4794CF, 7%)
                }
            }
            &:nth-child(4){
                background-color: #F7C71C;
                &:hover{
                    background-color: darken(#F7C71C, 7%)
                }
            }
            &:nth-child(5){
                background-color: #ED8325;
                &:hover{
                    background-color: darken(#ED8325, 7%)
                }
            }
            &:nth-child(6){
                background-color: #D279AF;
                &:hover{
                    background-color: darken(#D279AF, 7%)
                }
            }
            &:nth-child(7){
                background-color: #78C045;
                &:hover{
                    background-color: darken(#78C045, 7%)
                }
            }
            &:nth-child(8){
                background-color: #EC1B8D;
                &:hover{
                    background-color: darken(#EC1B8D, 7%)
                }
            }
            &:nth-child(9){
                background-color: #4794CF;
                &:hover{
                    background-color: darken(#4794CF, 7%)
                }
            }
            &:nth-child(10){
                background-color: #F7C71C;
                &:hover{
                    background-color: darken(#F7C71C, 7%)
                }
            }
            &:nth-child(11){
                background-color: #ED8325;
                &:hover{
                    background-color: darken(#ED8325, 7%)
                }
            }

            &:nth-child(12){
                background-color: #D279AF;
                &:hover{
                    background-color: darken(#D279AF, 7%)
                }
            }
                
            a {
                width: 100%;
                display: block;
                font-size: 16px;
                color: #FFF;
                transition: .5s;
            }

            i{
                position: absolute;
                top: 13px;
                left: 13px;
                transform: rotate(90deg);
                font-size: 9px;
                color: currentColor;
                transition: .5s;

                &.open{
                    transform: rotate(0deg);
                }
            }

            .dropDown{
                width: 100%;
                padding: 5px 15px;
                display: none;
                overflow: visible;
                height: auto;

                li{
                    padding: 4px 0;

                    a{
                        font-size: 15px;
                        color: #FFF;
                        transition: .5s;
                    }
                }
            }
        }
    }
}

