// Start Header
header{
    background-image: url('../images/head-back-night.png');
    background-repeat: no-repeat;
    position: relative;
    overflow: hidden;

    
    @include Md{
        background-position: center
    }
    
    @include Lg{
        background-position: right;
    }
    
    @include xLg{
        background-position: center;
        background-size: cover;
    }

    @include Sm{
        background-position: center;
        overflow: visible;
    }

    .snow{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -2;
    }

    .content{
        position: relative;
        z-index: 999;

        .shape{
            position: absolute;
            bottom: -133%;
            left: 50%;
            -webkit-transform: translateX(-50%);
            transform: translateX(-51%);
            z-index: -1;
        }
    }

    .top{
        padding: 10px 0 0;
        @include Flex;
        justify-content: space-between;
        margin-bottom: 30px;

        .menuTriger{
            font-size: 13px;
            font-family: "tj";
            color: #FFF;
            display: flex;
            align-items: center;
            i{
                font-size: 16px;
                color: #FFF;
                margin-left: 10px;

                html[dir="ltr"] & {
                    margin-right: 10px;
                    margin-left: 0;
                }
            }

            display: none;

            @include Sm{
                display: flex;
            }
        }

        .feature{
            font-size: 13px;
            font-family: "tjMd";
            color: rgb(255, 255, 255);

            @include Sm{
                display: none;
            }
        }

        .lang{
            font-size: 13px;
            font-family: "tj";
            color: rgb(255, 255, 255);
            text-transform: uppercase;
            transition: .5s;

            
            i{
                margin-right: 5px;
                
                html[dir="ltr"] & {
                    margin-right: 0;
                    margin-left: 5px;
                }
            }

            &:hover{
                color: rgba(255,255,255, .7);
            }
        }
    }

    .content2{
        @include Flex;
        justify-content: space-between;
        margin-bottom: 15px;

        @include Sm{
            flex-direction: column;
        }
    }

    .left{

        @include Sm{
            position: absolute;
            top: calc(100% + 15px);
            width: 100%;
            background-color: dodgerblue;
            display: flex;
            justify-content: center;
            padding: 10px 0;
            box-shadow: 200px 0 dodgerblue, -200px 0 dodgerblue;
        }

        .user-sec{
            @include Flex;

            @include Sm{
                display: none;
            }

            .sign{
                margin-bottom: 20px;

                a{
                    font-size: 14px;
                    font-family: "tjMd";
                    color: rgb(255, 255, 255);
                }

            }

            .myCart{
                align-self: baseline;
                margin-right: 17px;
                cursor: pointer;

                html[dir="ltr"] & {
                    margin-right: 0;
                    margin-left: 17px;
                }

                i{
                    color: #FFF;
                    font-size: 17px;
                }
            }
        }

        .search{
            position: relative;
            width: 241px;
            height: 36px;

            @include Sm{
                width: 100%;
            }

            input{
                width: 100%;
                height: 100%;
                border-radius: 100px;
                border: 0;
                padding: 0 20px;

                &::placeholder{
                    font-family: "tj";
                    font-size: 14px;
                    color: #bababa;
                }
            }

            button{
                position: absolute;
                top: 0;
                left: 0;
                width: 36px;
                height: 100%;
                border-top-left-radius: 100px;
                border-bottom-left-radius: 100px;
                border: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                background-color: #78C045;
                font-size: 14px;
                color: #FFF;
                overflow: hidden;
                
                html[dir="ltr"] & {
                    border-top-left-radius: 0;
                    border-bottom-left-radius: 0;
                    border-top-right-radius: 100px;
                    border-bottom-right-radius: 100px;
                    right: 0;
                    left: unset;
                }

                i{
                    transition: .3s;

                    &:last-child{
                        position: absolute;
                        top: -40%;
                        left: 50%;
                        transform: translateX(-50%);
                    }
                }

                &:hover{
                    i{

                        &:first-child{
                            transform: scale(1.3);
                            opacity: 0;
                            visibility: hidden;
                        }

                        &:last-child{
                            top: 50%;
                            transform: translateX(-50%) translateY(-50%);
                        }
                    }
                }
            }
        }
    }
}
// End Header

// Navbar
.navbar1{

    @include Sm{
        display: none;
    }

    & > ul{
        @include Flex;

        & > li{
            transition: .5s;
            
            a{
                display: block;
                width: 100%;
                height: 100%;
                font-size: 14px;
                color: #FFF;
                font-family: "tjMd";
                transition: .5s;
                
                @include Md{
                    padding: 10px 7px;
                }

                @include Lg{
