                }
            }

            &:nth-child(1){
                background-color: #78C045;
                border-radius: 0 0 10px 0;
                &:hover{
                    background-color: darken(#78C045, 7%)
                }
                
                html[dir="ltr"] & {
                    border-radius: 0 0 0 10px;
                }
            }
            &:nth-child(2){
                background-color: #EC1B8D;
                &:hover{
                    background-color: darken(#EC1B8D, 7%)
                }
            }
            &:nth-child(3){
                background-color: #4794CF;
                &:hover{
                    background-color: darken(#4794CF, 7%)
                }
            }
            &:nth-child(4){
                background-color: #F7C71C;
                &:hover{
                    background-color: darken(#F7C71C, 7%)
                }
            }
            &:nth-child(5){
                background-color: #ED8325;
                &:hover{
                    background-color: darken(#ED8325, 7%)
                }
            }
            &:nth-child(6){
                background-color: #D279AF;
                &:hover{
                    background-color: darken(#D279AF, 7%)
                }
            }
            &:nth-child(7){
                background-color: #78C045;
                &:hover{
                    background-color: darken(#78C045, 7%)
                }
            }
            &:nth-child(8){
                background-color: #EC1B8D;
                &:hover{
                    background-color: darken(#EC1B8D, 7%)
                }
            }
            &:nth-child(9){
                background-color: #4794CF;
                &:hover{
                    background-color: darken(#4794CF, 7%)
                }
            }
            &:nth-child(10){
                background-color: #F7C71C;
                &:hover{
                    background-color: darken(#F7C71C, 7%)
                }
            }
            &:nth-child(11){
                background-color: #ED8325;
                border-radius: 0 0 0 10px;
                &:hover{
                    background-color: darken(#ED8325, 7%)
                }

                html[dir="ltr"] & {
                    border-radius: 0 0 10px 0;
                }
            }

            &.drp{
                position: relative;

                .dropDown{
                    position: absolute;
                    top: 100%;
                    right: 0;
                    background-color: #EC1B8D;
                    width: 166px;
                    padding: 5px 11px;
                    z-index: 9;
                    transform: translateX(50px);
                    transition: .4s cubic-bezier(0.55, 0.085, 0.68, 0.53);
                    opacity: 0;
                    visibility: hidden;
                    
                    html[dir="ltr"] & {
                        left: 0;
                        right: unset;
                        transform: translateX(-50px);
                    }

                    ul{

                        li{
                            position: relative;
                            padding: 6px 10px;
                            border-top: 1px solid transparent;
                            border-bottom: 1px solid transparent;
                            transition: .5s;
                            overflow: hidden;
                            &:not(:last-child){
                                border-bottom: 1px solid #EE3198;
                            }

                            &:after,&:before{
                                content: "";
                                width: 100%;
                                height: 1px;
                                background-color: #F045A2;
                                position: absolute;
                                transition: .6s;
                            }

                            &:after{
                                top: calc(100% - 1px);
                                left: -100%;
                            }

                            &:before{
                                top: 0;
                                right: -100%;
                            }

                            a{
                                color: #FFF;
                                font-size: 14px;
                                font-family: "tjMd";
                                padding: 0;
                            }

                            &:hover{
                                background-color: #EE3198;

                                &:after{
                                    left: 0;
                                }

                                &:before{
                                    right: 0;
                                }
                            }
                        }
                    }
                }

                &:hover{

                    .dropDown{
                        transform: translateX(0px);
                        opacity: 1;
                        visibility: visible;
                        
                        html[dir="ltr"] & {
                            transform: translateX(0px);
                        }
                    }
                }
            }
        }
    }
}
