// Categories

.categories{
    position: absolute;
    z-index: 9;
    left: 50%;
    transform: translateX(-50%);
    
    @include Md{
        top: 58%;

        .owl-nav{
            display: none !important;
        }

        .owl-dots{
            top: 105%;
        }
    }
    
    @include Lg{
        top: 51%;
    }

    @include xLg{
        top: 60%;
    }

    @include Sm{
        top: 57%;
        .owl-dots{
            top: 105% !important;
        }
    }

    @include kh{
        top: 52%;
    }

    @include Xs{
        position: relative;
        left: unset;
        margin-top: -190px;
        transform: none;
        background-color: #FFF;
        padding-top: 20px;
    }
    
    @include mob600{
        margin-top: -100px;
    }

    @include mob375{
        margin-top: -70px;
        z-index: 999;
        position: relative;
        left: unset;
        background-color: #FFF;
        padding-top: 0px;
    }

    .content{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: auto;

        @include Md{
            width: 954px;
        }

        @include Xs{
            justify-content: center;
        }

        @include mob480{
            margin-top: 5px;
        }
    }

    .category{
        width: 220px;
        height: 180px;
        display: block;
        margin-bottom: 25px;
        overflow: hidden;
        border-radius: 10px;
        position: relative;

        @include Xs{
            width: calc((100% / 3) - 10px);

            &:not(:nth-of-type(3n)){
                margin-left: 15px;
                
                // html[dir="ltr"] &{
                //     margin-right: 15px;
                //     margin-left: 0;
                // }
            }
        }

        @include mob600{
            height: 150px;
        }

        @include mob480{
            width: calc((100% / 2) - 5px);
            margin-bottom: 5px;

            &:not(:nth-of-type(2n)){
                margin-left: 5px;

                html[dir="ltr"] &{
                    margin-right: 5px;
                    margin-left: 0;
                }
            }

            &:nth-of-type(2n){
                margin: 0;
            }
        }

        &:nth-child(5),&:nth-child(6),&:nth-child(7),&:nth-child(8){
            margin-bottom: 43px;
            @include mob480{
                margin-bottom: 5px;
            }
        }

        img{
            transition: .5s;

            @include Xs{
                width: 100%;
            }
        }

        p{
            margin: 0;
            position: absolute;
            bottom: 0;
            right: 0;
            width: 100%;
            height: 40px;
            background-color: $color-prim;
            @include Flex;
            font-family: "tjMd";
            font-size: 14px;
            color: #FFF;
            transition: .5s;
        }

        &:hover{
            p{
                background-color: darken($color-prim, 10%);
            }
            img{
                transform: scale(1.15) rotate(3deg);
            }
        }
    }
}

