.proTabs{
    margin-top: 50px;
    @include Flex;
    flex-wrap: wrap;

    @include mob600{
        height: 650px;
    }
    
    ul.nav-tabs{
        margin-bottom: 25px;
        padding: 0 30px;
        background-color: #78C045;
        border-radius: 100px;

        @include Md{
            padding: 0 20px;
        }

        @include Lg{
            padding: 0 30px;
        }

        @include Sm{
            padding: 0;
        }

        @include Xs{
            overflow: hidden;
        }

        @include mob375{
            border-radius: 0;
            position: absolute;
            top: -30px;
            width: 100%;
            right: 0px;
            overflow-x: auto;
            display: flex;
        }

        li{
            padding: 0;
            border: 0 !important;
            background-color: transparent !important;
            
            @include Xs{
                padding: 0;
            }
            
            @include mob375{
                padding: 0 14px;
                white-space: nowrap;
            }
            
            a{
                border: 0 !important;
                border-bottom: 2px solid transparent;
                font-size: 16px;
                color: #FFF;
                font-family: "tjMd";
                
                @include Xs{
                    font-size: 13px;
                }

                @include mob600{
                    padding: 10px;
                }

                @include mob375{
                    padding: 10px 0;
                    font-size: 16px;
                }
            }

            &.active, &:hover{
                border: 0 !important;
                background-color: transparent !important;

                & > a{
                    border: 0 !important;
                    background-color: transparent !important;
                    border-bottom: 2px solid #FFF !important;
                    color: #FFF;
                }
            }
        }
    }

    .tab-content{
        width: 100%;

        @include mob375{
            margin-top: -80px;
        }

        .owl-item{
            
            @include mob375{
                width: 180px !important;
            }
        }
    }
}

.product{
    box-shadow: 0px 1px 0px 0px rgba(187, 215, 219, 0.004);
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
    overflow: hidden;
    position: relative;
    padding: 25px 12px 12px;

    @include mob375{
        width: 180px;
    }

    .discount{
        width: 100%;
        height: 32px;
        background-color: #4794CF;
        position: absolute;
        top: 0;
        right: -40px;
        transform: rotate(35deg);
        padding-right: 40px;
        display: flex;
        align-items: center;
        font-size: 16px;
        font-family: "tjMd";
        color: #FFF;
    }

    .pic{
        width: 155px;
        height: 155px;
        @include Flex;

        margin: auto;
    }

    .title{
        font-size: 15px;
        font-family: "tj";
        color: rgb(26, 55, 102);
        line-height: 1.267;
        display: block;
        width: 100%;
        text-align: center;
        margin-top: 20px;
        margin-bottom: 11px;
    }

    .old-price{
        font-size: 16px;
        font-family: "tjMd";
        color: rgb(175, 175, 175);
        text-decoration: line-through;
        text-align: center;
    }

    .price{
        font-size: 18px;
        font-family: "tjBold";
        color: rgb(26, 55, 102);
        text-align: center;
    }

    .pro-quantity{
        position: relative;
        margin-top: 12px;
        margin-bottom: 8px;

        input{
            width: 100%;
            border-radius: 100px;
            border: 1px solid #C3E5EC;
            height: 32px;
            text-align: center;
            color: #a3a3a3;
            
            &::placeholder{
                color: #a3a3a3;
            }
        }

        button{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 20px;
            height: 20px;
            @include Flex;
            border: 0;
            background-color: transparent;

            i{
                font-size: 11px;
                color: #c3c3c3;
                transition: .5s;
            }

            &.plus{
                right: 12px;
            }

            &.minus{
                left: 12px;
            }

            &:hover{

                i{
                    color: $color-prim;
                }
            }
        }
    }

    .product-options{
        @include Flex;
        justify-content: space-between;

        .toCart{
            width: 123px;
            height: 32px;
            border-radius: 100px;
            background-color: #ED8325;
            border: 0;
            @include Flex;
            margin-left: 4px;
            font-size: 14px;
            font-family: "tjMd";
            color: rgb(255, 255, 255);
            transition: .5s;

            &:hover{
                background-color: #4794CF;
            }

            // html[dir="ltr"] & {
            //     margin-left: 0;
            //     margin-right: 4px;
            // }
        }

        .heart{
            width: 32px;
            height: 32px;
            border-radius: 50%;
            background-color: #C2C2C2;
            border: 0;
            @include Flex;
            font-size: 14px;
            color: #FFF;
            transition: .5s;

            &:hover{
                background-color: #dd042b;
                // color: #4794CF;
            }
        }
    }
}
// Products
// footer

footer{
    padding: 70px 0;
    padding-bottom: 60px;
    background-color: #4794CF;
    position: relative;

    .footer-intro{
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        width: 287px;
        height: 55px;
        border-radius: 100px;
        background-color: #71BF2C;
        font-size: 20px;
        font-family: "tjBold";
        color: rgb(255, 255, 255);
        @include Flex;
        // position: relative;

        @include mob375{
            width: 235px;
            font-size: 16px;
        }

        img{
            position: absolute;
            top: 10px;
            right: -45px;

            html[dir="ltr"] &{
                right: unset;
                left: -45px;
                transform: scaleX(-1);

            }
            @include mob375{
                width: 60px;    
            }
        }

    }

    @include Sm{
        padding: 40px 0;
        margin-top: 40px;
    }

    @include mob600{
        margin-top: 0;
    }

    .footer-map{
        position: relative;
        margin-bottom: 58px;

        & > a{
            display: block;
            width: 225px;
            height: 50px;
            border-radius: 100px;
            background-color: #ED8325;
            @include Flex;
            font-size: 20px;
            color: #FFF;
            font-family: "tjBold";
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 40px;

            html[dir="ltr"] & {
                left: auto;
                right: 40px;
            }

            @include Xs{
                display: none;
            }
        }
    }

    #map{
        border-radius: 10px;
    }

    .foot-links{

        // @include Lg{
        //     text-align: right;
        // }

        @include Sm{
            margin-bottom: 15px;
        }

        .collapse{
            display: block;

            @include Sm{
                display: none;
            }

            &.in{
                display: block;
            }
        }

        & > button{
            font-size: 16px;
            font-family: "tjBold";
            color: #fff;
            margin-bottom: 20px;
            background-color: transparent;
            border: 0;
            padding: 0;
            pointer-events: none;

            &.trans{

                &:after{
                    content: "\2212";
                }
            }

            @include Sm{
                width: 100%;
                margin: auto;
                pointer-events: all;
                margin-bottom: 0;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            @include mob480{
                width: 100%;
            }

            &:after{
                content: "\002B";
                margin-left: 5px;
                font-size: 18px;
                float: left;
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }

        ul{

            @include Sm{
                padding-top: 10px;
                padding-right: 10px;
            }

            li{
                &:not(:last-child){
                    margin-bottom: 10px;
                }
            }
        }

        a{
            font-size: 15px;
            font-family: "tj";
            color: #FFF;
            transition: .5s;

            &:hover{
                color: darken(#FFF, 15%);
            }
        }
    }

    .newsletter{

        & > button{
            font-size: 16px;
            font-family: "tjBold";
            color: #fff;
            margin-bottom: 20px;
            background-color: transparent;
            border: 0;
            padding: 0;
            pointer-events: none;
            
            &.trans{
                
                &:after{
                    content: "\2212";
                }
            }
            
            @include Sm{
                width: 100%;
                margin: auto;
                pointer-events: all;
                margin-bottom: 24px;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            @include mob480{
                width: 100%;
            }

            &:after{
                content: "\002B";
                margin-left: 5px;
                font-size: 18px;
                float: left;
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }
        
        @include Md{
            margin-top: 35px;
            text-align: center;
        }

        @include Lg{
            margin-top: 0;
            text-align: right;

            html[dir="ltr"] & {
                text-align: left;
            } 
        }

        @include Sm{
            margin-top: 0;
            text-align: center;
        }

        // & > p{
        //     font-size: 16px;
        //     font-family: "tjBold";
        //     color: #fff;
        //     margin-bottom: 24px;
        // }
    }

    .frm{
        position: relative;
        width: 360px;
        
        @include Md{
            margin: auto;
            margin-bottom: 22px;
        }

        @include Sm{
            margin: auto;
        }

        @include mob375{
            width: 280px;
        }

        input{
            padding: 0 20px;
            height: 45px;
            width: 100%;
            border: 0;
            color: #a3a3a3;
            border-radius: 100px;
            font-size: 14px;
            background-color: $color-white;
            transition: .5s;

            &::placeholder{
                color: #a3a3a3;
            }
        }

        button{
            position: absolute;
            top: 50%;
            left: 7px;
            transform: translateY(-50%);
            width: 33px;
            height: 33px;
            border-radius: 50%;
            border: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $color-sec;
            color: $color-white;
            overflow: hidden;
            transition: .5s;

            html[dir="ltr"] & {
                right: 7px;
                left: unset;
            }

            i{
                position: relative;
                z-index: 1;
                font-size: 13px;
            }

            &:after{
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                background-color: $color-prim;
                z-index: 0;
                width: 100%;
                height: 100%;
                transform: scaleX(0);
                transition: .5s;
            }

            &:hover{
                // border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
                // &:after{
                //     transform: scaleX(1);
                // }
            }
        }
    }

    .collapse{
        display: block;

        @include Sm{
            display: none;
        }
    }

    .social{
        @include Flex;

        @include Sm{
            margin-top: 22px;
        }
        
        a{
            @include Flex;
            width: 40px;
            height: 40px;
            background-color: #4683B0;
            border-radius: 50%;
            transition: .5s;

            i{
                font-size: 18px;
                color: #fff;
                transition: .5s;
            }

            &:not(:last-child){
                margin-left: 10px;

                html[dir="ltr"] & {
                    margin-right: 10px;
                    margin-left: 0;
                }
            }

            &:hover{
                // border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
                background-color: #71BF2C;

                i{
                    color: #FFF;
                }
            }
        }
    }
}

// End Of Footer

// End

.end{
    background-color: #4683B0;

    @include Sm{
        padding-top: 10px;
    }

    .container{
        // border-top: 1px solid #1b9a9b;
        padding: 0;
    }

    .content{
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include mob600{
            flex-direction: column;
        }

        @include Xs{
            text-align: center;
            flex-direction: column;
            padding-bottom: 10px;
        }


        p,a{
            font-size: 13px;
            color: #83e1e2;
            margin-bottom: 0;

            @include Xs{
                font-size: 13px;
                margin-bottom: 5px;
            }

            @include mob600{
                margin-bottom: 10px;
            }
        }

        p{

            @include mob480{
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column-reverse;
                margin-bottom: 0;
            }
        }

        a{
            @include Xs{
                order: -1;
            }

            img{
                margin-right: 5px;
            }

            @include mob480{
                margin-top: 10px;
            }
        }

        .social{


            a{
                font-size: 18px;
                transition: .5s;

                &:not(:last-child){
                    margin-left: 25px;
                }

                &:hover{
                    color: $color-white;
                }
            }
        }
    }
}

